<template>
  <section>
    <v-container fluid>
      <v-row>
        <v-col md="12">
          <PageHeader :title="headercontent.title" />
        </v-col>

        <v-col md="8">
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="form-section"
          >
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="title"
                  outlined
                  label="Emotion Title"
                  class="mr-btm-minus"
                  :rules="titleRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-select
                  v-model="status"
                  :items="statuses"
                  label="Status"
                  outlined
                  item-text="name"
                  item-value="status"
                ></v-select>
              </v-col>
              <v-col cols="12" class="submitbtn">
                <v-btn
                  :disabled="!valid"
                  class="submitbutton btn-dark"
                  @click="validate"
                  v-if="!allLoadingCurd"
                >
                  {{ headercontent.title }}
                </v-btn>
                <v-progress-circular
                  v-if="allLoadingCurd"
                  indeterminate
                ></v-progress-circular>
              </v-col>
            </v-row>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import PageHeader from "@/components/dashboard/PageHeader";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "AddEmotion",
  components: { PageHeader },
  data: () => ({
    valid: true,
    items: ["Foo", "Bar", "Fizz", "Buzz"],
    msg: "Vue Image Upload and Resize Demo",
    hasImage: false,
    image: null,
    title: "",
    description: "",
    sort: 0,
    headercontent: {
      title: "Add Emotion",
      addlink: "/dashboard/emotion/add",
      downloadlink: true,
    },
    content: "<h2>I am Example</h2>",
    editorOption: {
      // Some Quill options...
    },
    titleRules: [(v) => !!v || "Category Title is required"],
    statusRules: [(v) => !!v || "Status is required"],
    sortRules: {
      required: (value) => !!value || "Sort is required.",
      nan: (v) => !isNaN(v) || "Sort should be number",
    },
    descriptionRules: [(v) => !!v || "Description is required"],
    statuses: [
      { name: "Yes", status: true },
      { name: "No", status: false },
    ],
    status: true,
    imageUrl: null,
    id: null,
  }),
  methods: {
    ...mapActions(["addCategory", "fetchCategoryBySlug", "updateCategory"]),
    validate() {
      if (this.$refs.form.validate()) {
        let fd = new FormData();
        fd.append("title", this.title);
        fd.append("description", this.description);
        fd.append("sort", this.sort);
        fd.append("status", this.status);
        if (this.$route.params.slug == undefined) {
          if (this.image != null) {
            fd.append("image", this.image);
            fd.append("fileType", "images");
            fd.append("imageFile", "categories");
            this.addCategory(fd);
          } else {
            this.addCategory(fd);
          }
        } else {
          if (this.image != null) {
            fd.append("id", this.id);
            fd.append("image", this.image);
            fd.append("fileType", "images");
            fd.append("imageFile", "categories");
            this.updateCategory(fd);
          } else {
            if (this.imageUrl == null) {
              fd.append("id", this.id);
              this.updateCategory(fd);
            } else {
              console.log(this.imageUrl);
              fd.append("id", this.id);
              fd.append("imageUrl", this.imageUrl);
              this.updateCategory(fd);
            }
          }
        }
      }
    },

    setImage: function(output) {
      this.hasImage = true;
      this.image = output;
      console.log("Info", output.info);
      console.log("Exif", output.exif);
    },

    onEditorChange({ quill, html, text }) {
      console.log(quill, text);
      this.content = html;
    },
    removeImage() {
      this.imageUrl = "";
    },
  },
  computed: {
    ...mapGetters(["allLoadingCurd", "allCategory"]),
    editor() {
      return this.$refs.myQuillEditor.quill;
    },
  },

  async created() {
    await this.fetchCategoryBySlug(this.$route.params.slug);
     if (this.$route.params.slug != undefined) {
        this.title = this.allCategory.title;
        this.description = this.allCategory.description;
        this.sort = this.allCategory.sort;
        this.status = this.allCategory.status;
        this.imageUrl = this.allCategory.imageUrl;
        this.id = this.allCategory._id;
      }
  },
};
</script>

